.nav {
  width: 19.76vw;
  height: 100vh;

  background-color: #23262b;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  padding-left: 3vw;

  color: #828ea0;

  font-family: Gotham Pro;
  font-size: 14pt;

  z-index: 16;
  position: fixed;
}
.nav > * {
  height: 8vh;
  cursor: pointer;
}

.main-text-description {
  width: 59.8vw;
  position: absolute;
  right: 13vw;
  top: 10vh;
  margin-bottom: 10vh;
}

.title-name {
  font-size: 50pt;
  color: #828ea0;
  font-family: Gotham Pro;
  font-weight: bold;
}
.descr {
  font-size: 12pt;
  color: #828ea0;
  font-family: Gotham Pro;
  opacity: 0.5;
}

.positions_1 {
  font-size: 16pt;
  color: #828ea0;
  font-family: Gotham Pro;

  margin: 5vh 0 2vh 0;
}
.desktop--nav {
  width: 20vw;
  height: 100vh;
  background-color: #23262b;
  position: fixed;
  top: 0;
  left: 0;
}
.UA__header > .header-title {
  background: linear-gradient(-90deg, #1b1e23 80%, #23262b 20%);
}

@media screen and (max-width: 400px) {
  .UA__header>.header-title{
    background: #1b1e23;
  }
  .nav {
    width: 100vw;
    height: 100vh;
    padding-left: 20vw;
    z-index: 1;
  }
  .logotype-client_agr {
    display: none;
  }
  .UA__header > .header-title {
    background-color: inherit;
  }
  .title-name {
    font-size: 20pt;
    margin-bottom: 4vh;
  }
  .main-text-description {
    width: 70vw;
  }
  .main-text-description > div > .descr {
    font-size: 8pt;
  }
  .main-text-description > div > .positions_1 {
    font-size: 12pt;
  }

  .before_click {
    width: 50px;
    height: 70px;
    border-radius: 50%;
    background-color: #fff700;
    transform: translate(-50%, 45vh);
  }
  .before_click > * {
    display: none;
  }
  .menu-close {
    position: fixed;
    /* width: 60px; */
    top: 47.5vh;
    left: 10px;
    z-index: 1200;
  }
  .manip {
    width: 15px;
    height: 20px;

    padding-top: 15px;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .manip > .rect {
    width: 5px;
    height: 35px;
    border-radius: 2px;
    background-color: #23262b;
  }

  .open > .manip > .rect {
    background-color: #fff700;
  }

  .open > .manip > :first-child {
    transform: translateX(9px) rotate(45deg);
  }
  .open > .manip > :last-child {
    transform: rotate(-45deg);
  }
  .block-back {
    background: inherit;
  }
  .unblock-back {
    background: #1b1e23;
  }
}
