.mainBlock{
    width: 100vw;
    height: 100vh;
    background: url(https://i.ibb.co/jkH7q2F/Annotation-2020-01-01-202735.png) no-repeat;
    background-position: 100% 50%;
}
.mainPage{
    width: 70%;
    min-height: 50vh;

    margin: 12vh auto;
}
.mainPage>:first-child{
    font-size: 16pt;
    font-weight: bold;
}
.userI{
    display: grid;
    grid-template-columns: 1fr 4fr;
}
.userI>:first-child{
    width: 10vw;
    height: 10vw;

    border-radius: 50%;

    margin-top: 5vh;
}
.userI>:last-child{
    font-size: 24pt;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.userI>:last-child>:last-child{
    font-size: 16pt;
    font-weight: normal;
}