.page {
  background: url(https://i.ibb.co/tLCcFkt/Annotation-2020-01-01-202735.png);
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-color: #23262b;
}
.mainPageContainer {
  padding-top: 2vh;

  width: 85%;
  height: 100vh;
  margin-left: 2vw;

  display: grid;
  grid-template-areas:
    "exit activePairs activePairs activePairs activePairs activePairs"
    "menu moneyPairs moneyPairs moneyPairs moneyPairs moneyPairs"
    "menu moneyPairs moneyPairs moneyPairs moneyPairs moneyPairs"
    "menu moneyPairs moneyPairs moneyPairs moneyPairs moneyPairs"
    "menu moneyPairs moneyPairs moneyPairs moneyPairs moneyPairs"
    "menu moneyPairs moneyPairs moneyPairs moneyPairs moneyPairs"
    "menu moneyPairs moneyPairs moneyPairs moneyPairs moneyPairs";
}
.mainPageContainer > :first-child {
  grid-area: exit;
}
.mainPageContainer > :nth-child(2) {
  grid-area: activePairs;
  display: grid;
  grid-template-rows: 1fr 1fr;
}
.mainPageContainer > :nth-child(3) {
  grid-area: menu;
}
.mainPageContainer > :last-child {
  grid-area: moneyPairs;
}
.mainPageContainer > :first-child > a >img {
  width: 20px;
}

.searchContainer{
height: 5vh;
background: linear-gradient(-90deg, #828ea0 97%, #fff700 3%);
}

.selectedPairs{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.selectedPairs>*{
  margin: 0 10px;
}

.secondSearchContainer{
    width: 100%;
    height: 90%;
    display: grid;
    grid-template-columns: 3fr 97fr;
    background-color: #23262b;
}
.secondSearchContainer>img{
    margin: 1vh;
}
.search {
    background-color: #23262b;

    border: none;
    
    outline: none;

    font-size: 16pt;

    color: #828ea0;
}
.menuItems{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
    padding-top: 50%;
}
.menuItems>*{
    margin: 2vh 0;
    font-size: 16pt;
    cursor: pointer;
}
.active{
    color: #fff700;
}
.pairsCntainer{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.pairsCntainer>*{height: auto;}