* {
  color: #828ea0;
  font-family: Gotham Pro;
}
.mainContainer {
  width: 98%;
  margin: auto;
}
.main {
  height: 10vh;

  display: grid;
  grid-template-columns: 1fr 1fr 2fr 0.5fr 0.5fr 0.5fr;
  padding: 3vh;
  font-size: 15pt;
}
.logotype {
  width: 7vw;
}
.user,
.chartType,
.interval,
.accountType {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.user > svg {
  width: 2vh;
  height: 2vh;
  margin-right: 1vw;
}
.user > svg > g > path {
  fill: #828ea0;
}
.button {
  width: 100%;
  height: 5vh;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 10px;

  background-color: #fff700;
  color: initial;

  font-size: 15pt;
  font-weight: bold;
}

.currencyContainer {
  display: grid;
  grid-template-columns: 0.5fr repeat(10, 1fr);
  padding-left: 1vw;
}
.currencyPlus {
  display: flex;
  justify-content: center;
  align-items: center;
}
.currencyPlus > svg {
  height: 30px;
}
.currencyPlus > svg > path {
  fill: #828ea0;
}

.mainBigBlock {
  width: 100%;
  display: grid;
  grid-template-columns: 8fr 2fr;
}

.mainChartWithPrice {
  display: grid;
  grid-template-rows: 9fr 1fr;

  height: 85vh;
}

.chartBlock {
  margin-top: 2vh;

  width: 80vw;
  background-color: #111;

  display: flex;
  justify-content: center;
  align-items: center;
}

.price {
  width: 100%;
  display: grid;
  grid-template-columns: 0.6fr 1fr 1fr 1fr 1fr 1fr 1.5fr;
  margin-top: 10px;
}

.history {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 50%;

  font-size: 14pt;
}

.firstBlock {
  width: 50%;
  border-bottom: 3px solid #828ea0;
  margin: auto;
  margin-top: 12px;
}

.priceContainer {
  display: flex;
  height: 100%;

  justify-content: center;
  align-items: center;
}
.history{
  display: flex;
  flex-direction: column;
justify-content: center;
align-items: flex-start;

  height: 3vh;
  margin-top: 1vh;
  line-height: 0.7;
}
.red {
  color: #ff3a55;
}
.green {
  color: #00c873;
}
.selectVal {
  width: 80%;
  margin: auto;
  margin-top: 15px;
}

.posibleIncome {
  width: 80%;
  height: 30vh;
  margin: auto;

  display: flex;
  justify-content: left;
  align-items: center;

  font-size: 14pt;
}
.buttons {
  width: 80%;
  margin: auto;
}
.buttons > * > * {
  color: #1b1e23;
  font-size: 18pt;
  font-weight: bold;
}

/***********************************dropDown **********************************/
.ddMinutes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.ddMinutes > :last-child {
  width: 1.5vh;
  height: 1.5vh;

  border: 4px solid #828ea0;
  border-right: none;
  border-top: none;
  border-radius: 1px;

  margin-left: 1vh;
  margin-top: -0.5vh;

  transform: rotate(-45deg);
  cursor: pointer;
}
@keyframes rotateAround {
  from {
    transform: rotate(-45deg);
    color: #fff700;
  }
  to {
    transform: rotate(-225deg);
    color: #fff700;
  }
}
.active > :last-child,
.activeAccount > :last-child {
  border-color: #fff700;

  transform: rotate(-225deg);
  margin-top: 0;

  animation-name: rotateAround;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
}
.active {
  background-color: #23262b;
  width: 7vw;
  height: 5vh;
}

@keyframes show {
  from {
    height: 0;
  }
  to {
    height: 25vh;
  }
}
.ddMinutesContent {
  position: absolute;
  top: 8vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: #23262b;

  animation-name: show;
  animation-duration: 0.5s;
  overflow: hidden;
}
.ddMinutesContent > span {
  width: 7vw;
  height: 5vh;

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 15pt;
}
@keyframes showA {
  from {
    height: 0;
  }
  to {
    height: 12vh;
  }
}
.typesOfAccs {
  height: 6vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.typesOfAccs > :first-child,
.typesOfAccsCurrent > :first-child {
  font-size: 10pt;
}
.typesOfAccs > :last-child,
.typesOfAccsCurrent > :last-child {
  color: #fff700;
}
.ddAccountContent {
  position: absolute;
  top: 9vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: #23262b;

  animation-name: showA;
  animation-duration: 0.5s;
  overflow: hidden;
}
.ddAccount {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.ddAccount > :last-child {
  width: 1.5vh;
  height: 1.5vh;

  border: 4px solid #828ea0;
  border-right: none;
  border-top: none;
  border-radius: 1px;

  margin-left: 1vh;
  margin-top: -0.5vh;

  transform: rotate(-45deg);
  cursor: pointer;
}
.typesOfAccsCurrent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 3vw;
}
.accountType{
  width: 10vw;
  height: 6vh;

  margin-left: 3vw;
}
.activeAccount>:last-child{
  border-color: #fff700;

  transform: rotate(-225deg);
  margin-top: 0;

  animation-name: rotateAround;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
}
